import React, { Dispatch} from 'react'
import Affair from './Affair'
import {AffairType, FilterType} from './HW2'

export type DeleteAffairCallbackPropsType = (_id: number) => void

type AffairsPropsType = {
    data: AffairType[]
    setFilter: Dispatch<FilterType>
    deleteAffairCallback: DeleteAffairCallbackPropsType
}

function Affairs(props: AffairsPropsType) {
    const mappedAffairs = props.data.map((a: AffairType) => (
        <Affair // should work
            key={a._id} // –∫–µ–∏ –û–ë–Ø–ó–ê–¢–ï–õ–¨–ù–´ –≤ 99% - —Ç–∞–∫ —á—Ç–æ –ª—É—á—à–µ –∏—Ö –ø–∏—Å–∞—Ç—å –≤—Å–µ–≥–¥–∞ –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –≤ –º–∞–ø–µ
            affair={a}
            deleteAffairCallback={props.deleteAffairCallback}
        />
    ))

    const setAll = () => props.setFilter('all');
    const setHigh = () => props.setFilter('high');
    const setMiddle = () => props.setFilter('middle');
    const setLow = () => props.setFilter('low');

    return (
        <div>

            {mappedAffairs}

            <button onClick={setAll}>All</button>
            <button onClick={setHigh}>High</button>
            <button onClick={setMiddle}>Middle</button>
            <button onClick={setLow}>Low</button>
        </div>
    )
}

export default Affairs

